import React from 'react';

import './Home.sass';

const Home = () => (
  <div className="ui container home">
    <h1 className="ui header">About</h1>
    <h4>Front end developer</h4>
    <p>Front end matters by day, WebGL and WebVR doodling by night. Cherry picking libraries and frameworks from the frontend world and implementing lean and mean solutions is eating up all my time, the rest goes into and creative coding for the web, family time and basketball.</p>
  </div>
)

export default Home;



